@use '../../foundation/mixin' as mixin;

.c-gridA {
    background:url(/content/img/grid1.png) no-repeat center/cover;
}

.c-gridB {
    background:url(/content/img/grid2.png) no-repeat center/cover;
}

.c-gridC {
    background:url(/content/img/grid3.png) no-repeat center/cover;
    @include mixin.smartphone {
        background:url(/content/img/grid5.png) no-repeat center/cover;
    }
}

.c-gridD {
    background:url(/content/img/grid4.png) no-repeat center/cover;
    @include mixin.smartphone {
        background:url(/content/img/welovelogo.png) no-repeat center center / 95% 95%;
    }
}

.c-gridE {
    background:url(/content/img/grid5.png) no-repeat center/cover;
    @include mixin.smartphone {
        background:url(/content/img/grid3.png) no-repeat center/cover;
    }

}

.c-gridF {
    background:url(/content/img/grid6大.png) no-repeat center/cover;
    grid-column: 1 / 3;
    grid-row: 2 / 4;

    @include mixin.smartphone {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
    }
}

.c-gridG {
    background:url(/content/img/welovelogo.png) no-repeat center/cover;
    @include mixin.smartphone { 
        background:url(/content/img/grid4.png) no-repeat center/cover;
    }
}

.c-gridH {
    background:url(/content/img/grid8.png) no-repeat center/cover;
}

.c-gridI {
    background:url(/content/img/grid9.png) no-repeat center/cover;
}

.c-gridJ {
  background:url(/content/img/grid10.png) no-repeat center/cover;
}

.c-gridK {
  background:url(/content/img/grid11.png) no-repeat center/cover;
}

.c-gridL {
  background:url(/content/img/grid12.png) no-repeat center/cover;
}